<template>
  <div class="page-article" v-if="model">
    <div class="d-flex py-3 px-2 border-bottom">
      <div class="iconfont icon-back text-blue"></div>
      <strong class="flex-1 text-blue pl-2">
        {{model.title}}
      </strong>
      <div class="text-grey fs-xs">
        2020-03-23
      </div>
    </div>
    <div v-html="model.body" class="px-3 article-body fs-xl"></div>
    <div class="px-3 border-top py-3">
        <div class="d-flex ai-center">
          <i class="iconfont icon-cc-menu-circle"></i>
          <strong class="text-blue fs-xl ml-2">相关资讯</strong>
        </div>
        <div class="pt-2">
          <router-link 
          class="py-1 d-flex"
          tag="div" 
          :to="`/articles/${item._id}`" 
          v-for="item in model.related" 
          :key="item._id">
            <span class="flex-1 text-dark">{{item.title}}</span>
          <span class="text-grey-1 fs-md">{{model.createdAt | date}}</span>
          </router-link>
          
        </div>
    </div>
  </div>
</template>

<script>
import dayjs from 'dayjs'
export default {
  filters: { // 处理日期 通过dayjs工具把日期格式 进行转换(过滤器)
    date(val){
      return dayjs(val).format('MM/DD') // 两位数的月份和两位数的日期
    }
  },
  props: {
    id: {required: true}
  },
  data(){
    return{
      model: null
    }
  },
  watch: {
    id(){
      this.fetch()
    }
  },
  methods: {
    async fetch(){
      const res = await this.$http.get(`articles/${this.id}`)
      this.model = res.data;
    }
  },
  created(){
    this.fetch()
  }
};
</script>

<style lang="scss">
.page-article {
  .article-body{
    img{
      max-width: 100%;
      height: auto;
    }
    iframe{
      width: 100%;
      height: auto;
    }
  }
}
</style>